import './Header.css'

const Header = (props) => {
    var date = new Date();

    if (props.isButtons == 1) {
        return (
            <div id="header-wrapper">
                <h1 id="header" style={{ float: "left" }}>{props.text + "asda"}</h1>
                <button className="buttons">right</button>
                <button className="buttons">left</button>
            </div>
        );
    }
    else {
        return (
            <div>
                <h1 id="header">{props.text}</h1>
            </div>
        );
    }
}

export default Header;